<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div @click="click">打开DemoPage3</div>
  </div>
</template>

<script>
import { Router } from '../../core/router/Router'
document.title = 'DemoPage2'
export default {
  name: "DemoPage2",
  data() {
    return {
      msg: "I am DemoPage2!"
    };
  },
  methods: {
    click() {
      Router.navigate('/demo/page3')
    }
  }

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
</style>



